import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import UserModel from '../../models/UserModel';
import { Field, Formik } from 'formik';
import { Form } from 'react-router-dom';
import Swal from "sweetalert2";
import AppNotification from '../../helpers/AppNotification';


function ModalDetailUser(props) {
    const user = props.user;

    const handleClick = (role) => {
        // X l khi form c submit
        UserModel.updateRole({
            user_id : user.id,
            role : role,
        }).then(function (res) {
            Swal.fire({
                icon: "success",
                title: "Cp nht quyn thnh cng!",
                showConfirmButton: false,
                timer: 2000,
            });
            props.reloadPage();
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: "Np tin tht bi!",
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Np tin tht bi!');
        })
    };
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header>
                <Modal.Title id="contained-modal-title-vcenter">
                    Thng tin chi tit
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mb-3">
                    <div className='row'>
                        <div className='col-5 d-flex justify-content-center'>
                            <img src={user.image != null ? process.env.REACT_APP_URL_IMAGE + user.image : "/images/profile/profile.png"} style={{ width: '300px', height: '300px' }} alt="Image" />
                        </div>
                        <div className='col-6'>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    <strong className='text-success'>Tn:</strong>
                                </div>
                                <div className='col-6'>
                                    <p className=''>{user.name}</p>
                                </div>
                            </div>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    <strong className='text-success'>Email:</strong>
                                </div>
                                <div className='col-6'>
                                    <p className=''>{user.email}</p>
                                </div>
                            </div>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    <strong className='text-success'>S in thoi:</strong>
                                </div>
                                <div className='col-6'>
                                    <p className=''>{user.phone}</p>
                                </div>
                            </div>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    <strong className='text-success'>Username:</strong>
                                </div>
                                <div className='col-6'>
                                    <p className=''>{user.user_name}</p>
                                </div>
                            </div>
                            <div className='row pb-2'>
                                <div className='col-6'>
                                    <strong className='text-success'>S d ti khon:</strong>
                                </div>
                                <div className='col-6'>
                                    {user && user.account_balance &&
                                        <p className=''>{user.account_balance.toLocaleString('en-US')} VN</p>
                                    }
                                </div>
                            </div>
                            <form>
                                <div className='row pb-4'>
                                    <div className='col-6'>
                                        <strong className='text-warning'>Quyn truy cp:</strong>
                                    </div>
                                    <div className='col-6'>
                                        {
                                            user.role === 1 ?
                                                <strong className='text-warning'>Ngi dng</strong>
                                                :
                                                <strong className='text-warning'>Admin</strong>
                                        }
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                    {
                        user.role === 1 ?
                            <button type="button" className="btn btn-success" onClick={() => handleClick(0)}>
                                Chuyn qua Admin
                            </button>
                            :
                            <button type="button" className="btn btn-success" onClick={() => handleClick(1)}>
                                Chuyn qua ngi dng
                            </button>
                    }
                    <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                        ng
                    </button>
                </div>
            </Modal.Body>
        </Modal>
    );
}

export default ModalDetailUser;