import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import PaymentModel from '../../models/PaymentModel';
import ModalNotication from './ModalNotication';



function ModalOwnerBank(props) {

    const ownerBank = props.ownerbank;
    const amount = props.amount;
    const [modalShow, setModalShow] = React.useState(false);
    const handleCloseModal = () => {
        setModalShow(false);
    };
    console.log(props.dataQR);
    return (
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Np tin vo ti khon {ownerBank.name}:
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="mb-3">
                        <div className='row'>
                            <div className='col-5 d-flex justify-content-center'>
                                <img src={props.dataQR} style={{ width: '300px', height: '300px' }} alt="Image" />
                            </div>
                            <div className='col-6'>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>S ti khon:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.account_number}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Ch Ti khon:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.account_name}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>S tin:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{amount} VN</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Ni dung:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.note}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-warning'>Lu :</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-warning'>{ownerBank.note}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                            ng
                        </button>
                    </div>
                </Modal.Body>
            </Modal>

    );
}

export default ModalOwnerBank;