import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import BankItem from '../components/bank/BankItem';
import { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import { InputGroup } from 'react-bootstrap';
import React, { useRef } from 'react';
import ModalTransfer from '../components/paymentEarnmoney/ModalTransfer';
import ModalWithdraw from '../components/paymentEarnmoney/ModalWithdraw';
import PaymentModel from '../models/PaymentModel';
import AppNotification from '../helpers/AppNotification';
import * as Yup from "yup";
import VerifyCode from '../models/VerifyCode';
import { Field, Formik } from 'formik';
import Paginate from '../components/paginate/Paginate';



function PaymentEarnMoneyPage(props) {

    // paginate
    const [meta, setMeta] = useState({});
    const [perPage, setPerPage] = useState(5);
    const [page, setPage] = useState(1);
    const changePage = (page) => {
        setPage(page);
        setFilter({ search: keyword });
    }
    const handleSetPage = (number) => {
        setPage(page + number);
        setFilter({ search: keyword });
    }

    // search
    const [keyword, setKeyword] = useState('');
    const [filter, setFilter] = useState({
        search: '',
        perPage: perPage,
        page: page,
    });
    const handleChange = (event) => {
        setKeyword(event.target.value);
    };
    const handleSubmit = (event) => {
        event.preventDefault();
        setPage(1);
        // X l tm kim d liu da trn keyword
        setFilter({ search: keyword });
        // Reset trng input sau khi submit
    }


    // x l khc
    const [modalShow, setModalShow] = React.useState(false);

    const handleShowModal = (type) => {
        setModalShow(true)
    }
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const [modalShowTransfer, setModalShowTransfer] = React.useState(false);
    const handleShowModalTransfer = (type) => {
        setModalShowTransfer(true)
    }
    const handleCloseModalTransfer = () => {
        setModalShowTransfer(false);
    };

    const [info, setInfo] = useState({});
    const url_ref = process.env.REACT_APP_URL + info.user_name;

    const [withdrawHistory, setWithdrawHistory] = useState([]);

    useEffect(() => {
        PaymentModel.getWithdrawHistory({
            search: filter.search,
            page: page,
            perPage: perPage
        }).then((res) => {
            setMeta(res.meta);
            setWithdrawHistory(res.data);
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Ly tin tc tht bi!');
        })
    }, [page, filter])

    const inputRef = useRef(null);
    const copyToClipboard = () => {
        inputRef.current.select();
        document.execCommand('copy');
        inputRef.current.blur();
    };

    useEffect(() => {
        PaymentModel.getInfo().then((res) => {
            setInfo(res.data);
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Ly thng tin tht bi!');
        })
    }, [])

    const reloadPage = () => {
        window.location.reload();
    }


    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Kim tin" />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className="col-xl-6 col-lg-6">
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Gii Thiu Kim tin Triu</h4>
                                </div>
                                <div className="card-body">
                                    <div className="basic-form">
                                        <p>Link gii thiu ca bn:</p>
                                        <div className="input-group mb-3">
                                            <input ref={inputRef} type="text" className="form-control" value={url_ref} />
                                            <button className="btn btn-primary" type="button" onClick={copyToClipboard}> Sao chp </button>
                                        </div>
                                        <p><strong>S tin c th rt:</strong> <span style={{ marginLeft: '30px' }}>{info.can_earn_money} [Trc tip]</span></p>
                                        <p><strong>S ti khon con:</strong> <span style={{ marginLeft: '33px' }}>{info.count_account_chilrent} ti khon</span></p>
                                        <p><strong>Tng s khch np:</strong> <span style={{ marginLeft: '21px' }}>{info.count_people} ti khon</span></p>
                                        <p><strong>S tin kim c:</strong> <span style={{ marginLeft: '26px' }}>{info.total_earn_money} </span></p>
                                        <p><strong>Hoa hng trc tip:</strong> <span style={{ marginLeft: '23px' }}>{info.total_profit}% / n np thnh cng</span></p>
                                        <p><strong>S ln  rt tin:</strong> <span style={{ marginLeft: '33px' }}>{info.count_withdraw_money} ln rt</span></p>
                                        <p><strong>Tng s tin  rt:</strong> <span style={{ marginLeft: '23px' }}>{info.total_withdraw_money}</span></p>

                                        <div style={{ marginTop: '10px' }}>
                                            <button onClick={handleShowModal} className="btn btn-success">Rt tin</button>
                                            <ModalWithdraw
                                                show={modalShow}
                                                onHide={handleCloseModal}
                                                reloadPage={reloadPage}
                                            />
                                            <button onClick={handleShowModalTransfer} className="btn btn-outline-light" style={{ marginLeft: '10px' }}>Chuyn tin</button>
                                            <ModalTransfer
                                                show={modalShowTransfer}
                                                onHide={handleCloseModalTransfer}
                                                reloadPage={reloadPage}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className='col-xl-6'>
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Hng dn s dng</h4>
                                </div>
                                <div className="card-body p-3">
                                    <div className='row'>
                                        <div className='col-xl-12'>
                                            <iframe width="100%" height="315" src="https://www.youtube.com/embed/MHn66JJH5zs?si=-6UpbxTsSBnMWeMW" frameborder="0" allowfullscreen></iframe>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="card">
                        <div class="card-header">
                            <h4 class="card-title">Lch s rt tin v chuyn tin</h4>
                        </div>
                        <div class="card-body">
                            <div className='row'>
                                <div className='col-lg-12'>
                                    <form onSubmit={handleSubmit}>
                                        <div className='input-group mb-3'>
                                            <input
                                                type='text'
                                                className='form-control'
                                                placeholder='Nhp t kha...'
                                                value={keyword}
                                                onChange={handleChange}
                                            />
                                            <div className='input-group-append'>
                                                <button className='btn light btn-light' type='submit'>
                                                    Tm kim
                                                </button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                            <table class="table header-border table-responsive-sm">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>Th loi</th>
                                        <th>S tin</th>
                                        <th>Thc nhn</th>
                                        <th>Ghi ch</th>
                                        <th>Tnh trng</th>
                                        <th>Thi gian</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        withdrawHistory.map((item, key) => (
                                            <tr key={key}>
                                                <td>{item.id}</td>
                                                <td><span style={{ color: 'green' }} class="badge badge-warning">{item.type}</span></td>
                                                <td>{item.amount} </td>
                                                <td>{item.received} </td>
                                                <td>{item.note}</td>
                                                <td>{item.status === 1 ? <span class="badge badge-success">Thnh cng</span> : <span class="badge badge-danger">Ch duyt</span>}
                                                </td>
                                                <td><span class="text-muted">{item.create_at_fm}</span>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </table>
                            <Paginate meta={meta} changePage={changePage} page={page} handleSetPage={handleSetPage} />
                        </div>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default PaymentEarnMoneyPage;