import { Field, Form, Formik } from 'formik';
import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import * as Yup from 'yup';
import Swal from 'sweetalert2';
import UserBankAcountModel from '../../models/UserBankAcountModel';
import AppNotification from '../../helpers/AppNotification';
import BankModel from '../../models/BankModel';

const validationSchema = Yup.object().shape({
    amount: Yup.number()
        .required('Vui lng nhp s tin')
});

function ModalDepositApp(props) {
    const initialValues = {
        amount: '',
        user_bank_account_id: props.user_bank_account_id,
    };
    const handleSubmit = (values) => {
        BankModel.depositApp(values).then(function (res) {
            Swal.fire({
                icon: "success",
                title: "Np tin vo ti khon thnh cng!",
                showConfirmButton: false,
                timer: 2000,
                });
                props.reloadPage();
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: 'Np tin vo ti khon tht bi!',
                showConfirmButton: false,
                timer: 2000,
              });
            console.log(err);
        })
    };
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header>
                <Modal.Title id="contained-modal-title-vcenter">
                    Np Tin vo Ti khon {props.bankName}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleSubmit}
                >
                    {({ errors, touched }) => (
                        <Form>
                            <div className='card-body'>
                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>Tnh trng:</strong>
                                    </div>
                                    <div className="col">
                                        {props.BankStatus === 1 ? (
                                            <span className="badge badge-success">Hot ng</span>
                                        ) : (
                                            <span className="badge badge-danger">ang Tt</span>
                                        )}
                                    </div>
                                </div>

                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>ang S Dng:</strong>
                                    </div>
                                    <div className="col">
                                        {props.UserBankStatus === 1 ? (
                                            <span className="badge badge-success">Bt</span>
                                        ) : (
                                            <span className="badge badge-danger">Tt</span>
                                        )}
                                    </div>
                                </div>

                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>Gi Np tin vo ti khon:</strong>
                                    </div>
                                    <div className="col">
                                        20.000 + 0.1% tng bill / lt
                                    </div>
                                </div>
                            </div>
                            <div className="mb-3">
                                <label htmlFor="amount" className="form-label">
                                    <span className='text-success'>S ti khon ngn hng {props.bankName}</span> (*)
                                </label>
                                <Field
                                    type="text"
                                    id="amount"
                                    name="amount"
                                    className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                        }`}
                                    autoFocus
                                />
                                {errors.amount && touched.amount && (
                                    <div className="invalid-feedback">{errors.amount}</div>
                                )}
                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                                    Close
                                </button>
                                <button type="submit" className="btn btn-success">
                                    Xc nhn
                                </button>
                            </div>

                        </Form>
                    )}
                </Formik>
            </Modal.Body>
        </Modal>
    );
}

export default ModalDepositApp;