import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import PaymentModel from '../../models/PaymentModel';
import ModalOwnerBank from './ModalOwnerBank';
import Swal from "sweetalert2";

const validationSchema = Yup.object().shape({
    amount: Yup.number()
        .required('Vui lng nhp s tin')
        .min(10000, 'S tin phi ln hn hoc bng 10.000'),
});

function ModalDeposit(props) {

    const ownerBank = props.bank;
    const [amount, setAmount] = useState();
    const [ depositIdcheck, setDepositIdcheck ] = useState();
    const [ dataQR, setDataQR ] = useState();

    const [modalShow, setModalShow] = React.useState(false);
    const handleShowModal = () => {
        setModalShow(true);
    }
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const initialValues = {
        amount: '',
        ownerbank_id : props.ownerid,
    };

    const [ownerBankSD, setOwnerBankSD] = useState({});
    const [ownerBankSDchange, setOwnerBankSDchange] = useState();
    
    useEffect(() => {
        if (ownerBankSDchange) {
            setModalShow(true);
            setOwnerBankSDchange(false);
        }
    }, [ownerBankSD, ownerBankSDchange, amount,dataQR]);

    const handleSubmit = (values) => {
        PaymentModel.deposit(values).then(function (res) {
            setDepositIdcheck(res.data.id);
            setAmount(values.amount);
            setOwnerBankSD(ownerBank);
            setDataQR(res.QR_URI_data.data.qrDataURL);
            setOwnerBankSDchange(true);
            AppNotification.showSuccess("Gi yu cu Np tin thnh cng!");
            console.log(res.data);
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: "Np tin tht bi!",
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Np tin tht bi!!');
        })
    };
    

    useEffect(() => {
        const interval = setInterval(() => {
            PaymentModel.depositDetail(depositIdcheck).then(function (res) {
                AppNotification.showSuccess("ly chi tit np tin thnh cng!");
                if(res.data.status == 1){
                    UserModel.updateBlance({
                        amount: res.data.amount
                    }).then(function (res) {
                        Swal.fire({
                            icon: "success",
                            title: "Np tin thnh cng!",
                            showConfirmButton: false,
                            timer: 3000,
                        });
                        clearInterval(interval); 
                    }).catch(function (err) {
                        console.log(err);
                        AppNotification.showError('Cp nht s d tht bi!!');
                    })
                }
                console.log(res.data);
            }).catch(function (err) {
                console.log(err);
                AppNotification.showError('Ly chi tit tht bi!!');
            })
        }, 5000); 
        return () => {
          clearInterval(interval); 
        };
      }, [depositIdcheck , ownerBankSDchange]); 
    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        {ownerBank && `Np tin vo ti khon ${ownerBank.name}`}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={handleSubmit}
                    >
                        {({ errors, touched }) => (
                            <Form>
                                <div className="mb-3">
                                    <label htmlFor="amount" className="form-label">
                                        S tin (*)
                                    </label>
                                    <Field
                                        type="text"
                                        id="amount"
                                        name="amount"
                                        className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                            }`}
                                        autoFocus
                                    />
                                    {errors.amount && touched.amount && (
                                        <div className="invalid-feedback">{errors.amount}</div>
                                    )}
                                </div>

                                <div className="modal-footer">
                                    <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                                        Close
                                    </button>
                                    <button type="submit" className="btn btn-success">
                                        Xc nhn
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </Modal.Body>
            </Modal>
            <ModalOwnerBank
                show={modalShow}
                onHide={handleCloseModal}
                ownerbank={ownerBankSD}
                amount={amount}
                dataQR={dataQR}
            />
        </>


    );
}

export default ModalDeposit;