import React, { useEffect, useState } from 'react';
import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import UserModel from '../models/UserModel';
import AppNotification from '../helpers/AppNotification';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from "yup";
import { useNavigate } from 'react-router-dom';
import Swal from "sweetalert2";


const updateSchema = Yup.object().shape({
    phone: Yup.string()
        .min(10, "Ti thiu 10 s")
        .max(255, "Ti a 255 k t")
        .required("Trng bt buc"),
    user_name: Yup.string()
        .max(255, "Ti a 255 k t ")
        .required("Trng bt buc"),
    name: Yup.string()
        .max(255, "Ti a 255 k t ")
        .required("Trng bt buc"),
    email: Yup.string()
        .required("Trng bt buc"),
});

function UserProfilePage(props) {
    const [form, setForm] = useState({
        name: '',
        phone: '',
        email: '',
        user_name: ''
    })

    useEffect(() => {
        UserModel.find().then(function (res) {
            setForm(res.data);
        }).catch(function (error) {

        })
    }, [])

    const handleSubmit = (values) => {
        UserModel.update(values).then(function (res) {
            Swal.fire({
                icon: "success",
                title: "Cp Nht Profile thnh cng!",
                showConfirmButton: false,
                timer: 2000,
            });
            reloadPage();
            AppNotification.showSuccess('Cp Nht Profile thnh cng!');
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: "Cp Nht Tht Bi!",
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Cp Nht Tht Bi!');
        })
    }

    const reloadPage = () => {
        window.location.reload();
    };

    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Thng tin c nhn" />
            <div className="row">
                <div className="col-lg-12">
                    <div className="profile card card-body px-3 pt-3 pb-0">
                        <div className="profile-head">
                            {/* <div className="photo-content">
                                <div className="cover-photo rounded" />
                            </div> */}
                            <div className="profile-info">
                                <div className="profile-photo mt-2 mb-2">
                                    <img
                                        src={form.image != null ? process.env.REACT_APP_URL_IMAGE + form.image : "/images/profile/profile.png"}
                                        className="img-fluid rounded-circle"
                                        alt=""
                                    />
                                </div>
                                <div className="profile-details">
                                    <div className="profile-name px-3 pt-2 mt-3 mb-3">
                                        <h4 className="text-white mb-0">{form.name}</h4>
                                        <h4 className="mb-0">{form.email}</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-12">
                    <div className="card">
                        <div className="card-header">
                            <h4 className="card-title">Thng tin ti khon</h4>
                        </div>
                        <div className="card-body">
                            <div className="form-validation">
                                <Formik
                                    initialValues={form}
                                    enableReinitialize={true}
                                    validationSchema={updateSchema}
                                    onSubmit={(values) => handleSubmit(values)}
                                >
                                    {({ errors, touched }) => (
                                        <Form className="needs-validation" noValidate>
                                            <div className="row">
                                                <div className="col-xl-6">
                                                    <div className="mb-3 row">
                                                        <label className="col-lg-4 col-form-label" htmlFor="id">
                                                            Tn
                                                            <span className="text-danger">*</span>
                                                        </label>
                                                        <div className="col-lg-6">
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="name"
                                                                name="name"
                                                                placeholder="65cd8e9826f768edd202f12d"
                                                            />
                                                            {errors.name && touched.name ? (
                                                                <div style={{ color: 'red' }}>{errors.name}</div>
                                                            ) : null}
                                                        </div>
                                                    </div>
                                                    <div className="mb-3 row">
                                                        <label className="col-lg-4 col-form-label" htmlFor="phone">
                                                            phone
                                                            <span className="text-danger">*</span>
                                                        </label>
                                                        <div className="col-lg-6">
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="phone"
                                                                name="phone"
                                                                placeholder="65cd8e9826f768edd202f12d"
                                                            />
                                                            {errors.phone && touched.phone ? (
                                                                <div style={{ color: 'red' }}>{errors.phone}</div>
                                                            ) : null}
                                                        </div>
                                                    </div>
                                                    <div className="mb-3 row">
                                                        <label className="col-lg-4 col-form-label" htmlFor="verificationAccount">
                                                            Ti khon xc minh:
                                                            <span className="text-danger">*</span>
                                                        </label>
                                                        <div className="col-lg-6">
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="verificationAccount"
                                                                name="verificationAccount"
                                                                placeholder="65cd8e9826f768edd202f12d"
                                                                value={form.status === 0 ? "Cha xc minh" : " xc minh"}
                                                                readOnly={true}
                                                            />
                                                            <ErrorMessage
                                                                name="verificationAccount"
                                                                component="div"
                                                                className="invalid-feedback"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-xl-6">
                                                    <div className="mb-3 row">
                                                        <label className="col-lg-4 col-form-label" htmlFor="account">
                                                            Ti khon
                                                            <span className="text-danger">*</span>
                                                        </label>
                                                        <div className="col-lg-6">
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="user_name"
                                                                name="user_name"
                                                                placeholder="65cd8e9826f768edd202f12d"
                                                            />
                                                            {errors.user_name && touched.user_name ? (
                                                                <div style={{ color: 'red' }}>{errors.user_name}</div>
                                                            ) : null}
                                                        </div>
                                                    </div>
                                                    <div className="mb-3 row">
                                                        <label className="col-lg-4 col-form-label" htmlFor="email">
                                                            Email
                                                            <span className="text-danger">*</span>
                                                        </label>
                                                        <div className="col-lg-6">
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="email"
                                                                name="email"
                                                                placeholder="65cd8e9826f768edd202f12d"
                                                            />
                                                            {errors.email && touched.email ? (
                                                                <div style={{ color: 'red' }}>{errors.email}</div>
                                                            ) : null}
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="mb-3 row">
                                                    <div className="col-lg-12">
                                                        <button type="button" className="btn btn-default m-3">Khi phc</button>
                                                        <button type="submit" className="btn btn-primary">Cp nhp</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </Form>
                                    )}
                                </Formik>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default UserProfilePage;