import React, { useEffect, useState } from 'react';
import UserBankModel from '../../models/UserBankModel';
import Swal from "sweetalert2";
import AppNotification from '../../helpers/AppNotification';
import ModalCreateAcount from './ModalCreateAcount';
import UserBankAcountModel from '../../models/UserBankAcountModel';
import ModalDepositApp from './ModalDepositApp';


function BankInfo(props) {

    // show modal
    const [modalShow, setModalShow] = React.useState(false);
    const [modalDepositApp, setModalDepositApp] = React.useState(false);

    // const [listUserBankAcount, setListUserBankAcount] = useState({});

    var bankInfo = props.bankInfo;
    const [userBank, setUserbank] = useState({
        user_status: '',
        bank_id: '',
    });
    const [checkClick, setCheckClick] = useState(false);
    const changUserBankStatus = (number) => {
        setUserbank({
            user_status: number,
            bank_id: bankInfo.id,
        });
        setCheckClick(true);
    }
    useEffect(() => {
        if (checkClick) {
            UserBankModel.updateStatus(userBank)
                .then((res) => {
                    // Notification
                    AppNotification.showSuccess('Cp nht thnh cng!');
                    Swal.fire({
                        icon: "success",
                        title: "Cp nht thnh cng!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                    props.reloadPage();
                })
                .catch((err) => {
                    console.log(err);
                    AppNotification.showError('Cp nht tht bi!');
                    Swal.fire({
                        icon: "error",
                        title: "Cp nht tht bi!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                });
        }
    }, [userBank, checkClick]);

    const reloadPage = () => {
        window.location.reload();
    };

    return (
        <>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Thng tin ngn hng {bankInfo.name}</h4>
                        </div>
                        <div className='card-body'>
                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Ngn Hng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.name}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Tnh trng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.status === 1 ? (
                                        <span className="badge badge-success">Hot ng</span>
                                    ) : (
                                        <span className="badge badge-danger">ang Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>ang S Dng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.user_status === 1 ? (
                                        <span className="badge badge-success">Bt</span>
                                    ) : (
                                        <span className="badge badge-danger">Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi m ngn hng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.opening_fee}  (Vnh Vin)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi m ti khon:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.account_opening_fee}  (t ti khon th 2)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>M ti a:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.max_accounts} ti khon
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Gi np tin vo TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_deposit_fee}  + {bankInfo.app_deposit_fee_percentage}% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Gi chuyn tin trong TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_transfer_fee}  + {bankInfo.app_transfer_fee_percentage}% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi kim tra s ti khon t ng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.auto_check_account_fee}  / lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className="text-warning">Lu  quan trng:</strong>
                                </div>
                                <div className="col text-warning">
                                    {bankInfo.important_note}
                                </div>
                            </div>
                            <div className='p-3'>
                                <div
                                    className="toolbar toolbar-bottom"
                                    role="toolbar"
                                    style={{ textAlign: "right" }}
                                >
                                    {
                                        bankInfo.user_status === 1 ?
                                            <button className="btn btn-danger sw-btn-next btn-sm" type="button" onClick={() => changUserBankStatus(0)}>
                                                ng Ngn Hng
                                            </button>
                                            :
                                            <button className="btn btn-success sw-btn-next btn-sm" type="button" onClick={() => changUserBankStatus(1)}>
                                                M Ngn Hng
                                            </button>
                                    }
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {bankInfo.user_status === 1 ? (
                <div className='row'>
                    <div className='col-xl-12'>
                        <div className='card'>
                            <div className="card-header">
                                <h4 className="card-title">Danh sch ti khon</h4>
                            </div>
                            <div className='card-body'>
                                <div id="example_wrapper" className="dataTables_wrapper">
                                    <div className='row'>
                                        <div className='col-6'>
                                            
                                        </div>
                                        <div className='col-6 pb-3'>
                                            <div
                                                className="toolbar toolbar-bottom"
                                                role="toolbar"
                                                style={{ textAlign: "right" }}
                                            >
                                                {
                                                    props.listUserBankAcount == null ?
                                                        <button className="btn btn-primary sw-btn-next btn-sm" type="button" onClick={() => setModalShow(true)}>
                                                            Thm Ti Khon
                                                        </button>
                                                        : ""
                                                }
                                            </div>
                                        </div>
                                    </div>
                                    <div className='row d-flex pb-3' style={{ fontSize: '14px' }}>
                                        <div className='col-2'>
                                            <span className='text-warning'>S ti khon</span>
                                        </div>
                                        <div className='col-3'>
                                            <span className='text-warning'>Ch ti khon</span>
                                        </div>
                                        <div className='col-2'>
                                            <span className='text-warning'>S tin(VN)</span>
                                        </div>
                                        <div className='col-2'>
                                            <span className='text-warning'>Tnh Trng</span>
                                        </div>
                                        <div className='col-3'>
                                            <span className='text-warning'>Np tin-Chuyn i</span>
                                        </div>
                                    </div>
                                    {
                                        props.listUserBankAcount != null ?
                                            <div className='row' style={{ fontSize: '14px' }}>
                                                <div className='col-2'>
                                                    <span className='text-primary'>{props.listUserBankAcount.bank_number}</span>
                                                </div>
                                                <div className='col-3'>
                                                    <span className='text-primary'>{props.listUserBankAcount.bank_username}</span>
                                                </div>
                                                <div className='col-2'>
                                                    {props.listUserBankAcount && props.listUserBankAcount.account_balance &&
                                                        <span className='text-primary'>{props.listUserBankAcount.account_balance.toLocaleString('en-US')}</span>
                                                    }
                                                </div>
                                                <div className='col-2'>
                                                    {props.listUserBankAcount.status === 1 ? (
                                                        <span className="badge badge-success">Hot ng</span>
                                                    ) : (
                                                        <span className="badge badge-danger">ang Tt</span>
                                                    )}
                                                </div>
                                                <div className='col-3'>
                                                    <div className='row'>
                                                        <div className='card-body d-flex pt-0'>
                                                            <button type="button" className="btn btn-rounded btn-primary btn-sm"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-check2" viewBox="0 0 16 16">
                                                                <path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0" />
                                                            </svg></button>
                                                            <button onClick={() => setModalDepositApp(true)} type="button" className="btn btn-rounded btn-secondary btn-sm ms-2"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus-lg" viewBox="0 0 16 16">
                                                                <path fill-rule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2" />
                                                            </svg></button>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            :
                                            ""
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            ) : (
                ""
            )}
            <ModalCreateAcount
                show={modalShow}
                onHide={() => setModalShow(false)}
                bankName={bankInfo.name}
                BankStatus={bankInfo.status}
                UserBankStatus={bankInfo.user_status}
                bin={bankInfo.bin}
                type={bankInfo.type}
                reloadPage={reloadPage}
            />
            <ModalDepositApp
                show={modalDepositApp}
                onHide={() => setModalDepositApp(false)} 
                bankName={bankInfo.name}
                BankStatus={bankInfo.status}
                UserBankStatus={bankInfo.user_status}
                bin={bankInfo.bin}
                type={bankInfo.type}
                user_bank_account_id={!props.listUserBankAcount ? null : props.listUserBankAcount.id}
                reloadPage={reloadPage}
            />
        </>
    );
}

export default BankInfo;