import React, { useEffect, useState } from 'react';
import MasterLayout from '../../layouts/MasterLayout';
import ThemeBreadcrumb from '../../components/themes/ThemeBreadcrumb';
import AppNotification from '../../helpers/AppNotification';
import TransactionModel from '../../models/TransactionModel';
import Paginate from '../../components/paginate/Paginate';
import ModalStatus from '../../components/transaction/ModalStatus';
import Swal from 'sweetalert2';

function TransactionHistoryPage(props) {
    const [items, setItems] = useState([]);
    const [meta, setMeta] = useState({});
    const [page, setPage] = useState(1);
    const [perPage, setPerPage] = useState(5);
    const [keyword, setKeyword] = useState('');
    const [modalShowStatus, setModalShowStatus] = React.useState(false);
    const [element, setElement] = useState({});
    const [filter, setFilter] = useState({
        search: '',
        perPage: perPage,
        page: page,
    });

    const changePage = (page) => {
        setPage(page);
        setFilter({ search: keyword });
    };
    useEffect(() => {
        TransactionModel.history({
            search: filter.search,
            page: page,
            perPage: perPage
        })
            .then(res => {
                setItems(res.data);
                setMeta(res.meta);
            }).catch(err => {
                console.log(err);
                AppNotification.showError('Ly d liu tht bi!');
            })
    }, [page, filter]);
    const handleChange = (event) => {
        setKeyword(event.target.value);
    };
    const handleSubmit = (event) => {
        event.preventDefault();
        setPage(1);
        // X l tm kim d liu da trn keyword
        setFilter({ search: keyword });
        // Reset trng input sau khi submit
    };
    const handleSetPage = (number) => {
        setPage(page + number);
        setFilter({ search: keyword });
    };
    const handleShowModalStatus = (data) => {
        setElement(data);
        setModalShowStatus(true);
    };
    const handleCloseModalStatus = () => {
        setModalShowStatus(false);
    };
    const reloadPage = () => {
        window.location.reload();
    }
    const HandleShowModel = (id) => {
        TransactionModel.find(id).then(function (res) {
            if (res.success === true) {
                handleShowModalStatus(res.data);
            } else {
                Swal.fire({
                    icon: "error",
                    title: res.data,
                    showConfirmButton: false,
                    timer: 2000,
                });
            }
        });
    }
    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Lch S Giao Dch" />
            <div className='row'>
                <div class="col-lg-12">
                    <div class="card">
                        <div class="card-header">
                            <h4 class="card-title">Lch s giao dch</h4>
                        </div>
                        <div class="card-body">
                            <div className='row'>
                                <div className='col-lg-12'>
                                    <form onSubmit={handleSubmit}>
                                        <div className='input-group mb-3'>
                                            <input
                                                type='text'
                                                className='form-control'
                                                placeholder='Nhp t kha...'
                                                value={keyword}
                                                onChange={handleChange}
                                            />
                                            <div className='input-group-append'>
                                                <button className='btn light btn-light' type='submit'>
                                                    Tm kim
                                                </button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                            <table class="table header-border table-responsive-sm">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>Th Loi</th>
                                        <th>S tin</th>
                                        <th>Trng thi</th>
                                        <th>Ghi ch</th>
                                        <th>Thi gian</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        items.map((item, key) => (
                                            <tr key={key}>
                                                <td><a href="">{item.id}</a>
                                                </td>
                                                <td><span class="badge badge-danger">{item.type}</span>
                                                </td>
                                                <td>{item.amount}</td>
                                                <td>{item.status === 1 ? <span class="badge badge-success">Thnh cng</span> : <span class="badge badge-danger">Ch duyt</span>}
                                                </td>
                                                <td>{item.note}</td>
                                                <td><span class="text-muted">{item.create_at_fm}</span>
                                                </td>
                                                <td>{item.status === 0 ?  <button className='btn-sm btn-primary' onClick={()=>HandleShowModel(item.id)} >Cp Nhp</button> : ''}</td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </table>
                            <Paginate meta={meta} changePage={changePage} page={page} handleSetPage={handleSetPage} />
                        </div>
                    </div>
                </div>
            </div>
            <ModalStatus
                show={modalShowStatus}
                onHide={handleCloseModalStatus}
                reloadPage={reloadPage}
                element={element}
            />
        </MasterLayout>
    );
}

export default TransactionHistoryPage;
