import React, { useEffect, useState } from 'react';
import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import HomeCardMoney from '../components/home/HomeCardMoney';
import HomeModel from '../models/HomeModel';
import AppNotification from '../helpers/AppNotification';
import HomeCardInfo from '../components/home/HomeCardInfo';
import NewModel from '../models/NewsModel';
import CardNews from '../components/home/CardNews';

function HomePage(props) {
    const [data,setData] = useState([]);
    const [news,setNews] = useState([]);

    useEffect( () => {
        HomeModel.getDashboard().then( (res) => {
            setData(res.data);
        } ).catch( (err) => {
            console.log(err);
            AppNotification.showError('Ly d liu tht bi!');
        })
    },[])
    
    useEffect( () => {
        NewModel.getNews().then( (res) => {
            setNews(res.data);
        } ).catch( (err) => {
            console.log(err);
            AppNotification.showError('Ly tin tc tht bi!');
        })
    },[])

    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Bng iu khin" />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className="col-xl-3 col-lg-6 col-md-6 col-sm-6">
                            <HomeCardMoney title={'S tin  np vo h thng'} value={data.count_deposit} />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-md-6 col-sm-6">
                            <HomeCardMoney title={'S tin  chuyn qua ngn hng'} value={data.count_paymoney} />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-md-6 col-sm-6">
                            <HomeCardMoney title={'S tin np vo TK N.Hng'} value={data.total_money_pay} />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-md-6 col-sm-6">
                            <HomeCardMoney title={'S tin kim tra STK N.Hng'} value={data.total_money_check} />
                        </div>
                    </div>
                    <div className='row'>
                        <div className="col-xl-3 col-lg-6 col-sm-6">
                            <HomeCardInfo 
                                title_one={"Ngn hng  m: "}
                                value_one = {data.count_openbank}
                                title_two = {"S bill Chuyn khon  to: "}
                                value_two = {data.count_bill_paymoney}
                             />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-sm-6">
                            <HomeCardInfo 
                                title_one={"Ti khon  m: "}
                                value_one = {data.count_open_account}
                                title_two = {"S bill S d  to: "}
                                value_two = {data.count_bill_account_balance}
                             />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-sm-6">
                            <HomeCardInfo 
                                title_one={"S ti khon con: "}
                                value_one = {data.count_account_chilrent}
                                title_two = {"S bill Bin ng  to: "}
                                value_two = {data.count_bill_fluctuations}
                             />
                        </div>
                        <div className="col-xl-3 col-lg-6 col-sm-6">
                            <HomeCardInfo 
                                title_one={"S ti khon con np tin: "}
                                value_one = {data.count_account_chilrent_deposit}
                                title_two = {"Tng s tin to bill: "}
                                value_two = {data.total_money_create_bill_chilrent}
                             />
                        </div>
                    </div>

                    <div className='row'>
                        <div className='col-lg-12'>
                            <div className="card">
                                <div className="card-header border-0 pb-0">
                                    <h4 className="card-title">Tin tc mi nht</h4>
                                </div>
                                <div className="card-body">
                                    <div id="DZ_W_TimeLine" className="widget-timeline dz-scroll">
                                        <ul className="timeline">
                                        {
                                            news.map((item, key) => (
                                                <li key={key}>
                                                    <CardNews title={item.title} content={item.content} />
                                                </li>
                                            ))
                                        }
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>


                    </div>

                </div>
            </div>
        </MasterLayout>
    );
}

export default HomePage;